import {ActivityLevel} from "../App";


export const getActivityRecommendation = (fa: ActivityLevel, degree: string) => {
    const d = parseInt(degree)

    if (fa === ActivityLevel.LOW) return "   ,              ,       ,            (  !)      (  /      / )."
    if (fa === ActivityLevel.MIDDLE && d < 50) return " ,   ( .  , )."
    if (fa === ActivityLevel.MIDDLE && d > 50) return " ,  ,       ,      , .   ."
    if (fa === ActivityLevel.HIGH && d < 50) return " ,   ( ,  , )."

    return " ,   (       ,   ,  ),       .   . "
}